import React, { Component } from 'react';
import * as Ons from 'react-onsenui'

class ActivityPage extends Component {
  constructor(props) {
    super(props);

    this.renderToolbar = this.renderToolbar.bind(this);
  }

  renderToolbar() {
    return (
      <Ons.Toolbar>
        <div className="left">
          <Ons.BackButton></Ons.BackButton>
        </div>
        <div className="center">
          Tourism Moose Jaw App
        </div>
        <div className='right'>
          <Ons.ToolbarButton>
            <Ons.Icon icon='ion-navicon, material:md-menu'></Ons.Icon>
          </Ons.ToolbarButton>
        </div>
      </Ons.Toolbar>
    );
  }

  componentWillUnmount() {
    this.props.setDone(this.props.id);
  }

  render() {
    return (
      <Ons.Page renderToolbar={this.renderToolbar}>
        <Ons.Card he>
          <div className="title center">Task Title</div>
          <div className="content">
            Task detailed description
            <div style={{marginTop: '2rem', textAlign: 'center'}}>
              <label>
                <span style={{border: '1px solid grey', padding: '10px'}}>Take a picture</span>
                <input style={{display: 'none'}} type="file" accept="image/*;capture=camera"/>
              </label>
            </div>
          </div>
        </Ons.Card>
      </Ons.Page>
    );
  }
}

export default ActivityPage;